* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  display: block;
  margin: 10px;
  position: fixed;
  width: 99%;
  align-items: end;
  z-index: 2;
}
body {
  height: 100vh;
  width: 100%;
  font-family: "Open Sans", sans-serif;
  background: #444;
}

.slider {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100%;
}

.slider--content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
}

.slider--feature {
  text-align: center;
  background: black;
  color: white;
}

.feature--title {
  text-align: center;
  font-size: 3.5rem;
  text-align: center;
  background: black;
  text-transform: uppercase;
  font-weight: 700;
}

.feature--text {
  padding: 0.1rem 2rem;
  font-size: 4rem;
  color: #fff;
  margin: 1rem 0;
}

.slider__btn-right {
  background: #7d5d3b;
  outline: #7d5d3b;
  font-size: 2rem;
  font-color: white;
  color: #eee;
  padding: 0 1rem;
  cursor: pointer;
  transition: transform 0.1s ease-in-out;
}

.slider__btn-left {
  background: #7d5d3b;
  outline: #7d5d3b;
  font-size: 2rem;
  font-color: white;
  color: #eee;
  padding: 0 1rem;
  cursor: pointer;
  transition: transform 0.1s ease-in-out;
}

.feature__btn {
  background: #7d5d3b;
  text-transform: uppercase;
  border: none;
  color: white;
  border: 1px solid #444;
  outline: none;
  font-weight: 700;
  padding: 0.8rem 2rem;
  cursor: pointer;
}

.slider__btn-left:hover,
.slider__btn-right:hover {
  transform: scale(0.95);
  text-align: center;
  background: #7d5d3b;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.fadeIn {
  -webkit-animation: fadeIn 0.5s;
          animation: fadeIn 0.5s;
}
.loading {
  color: #3c240f;
  font-family: "Playfair Display", serif;
}

h1 {
  font-size: 1rem;
